import { Component, OnInit } from '@angular/core';
import {FormGroup, Validators, FormBuilder} from '@angular/forms';
import {trigger, state, style, transition, animate} from '@angular/animations';

@Component({
  selector: 'app-gallery-layout',
  templateUrl: './gallery-layout.component.html',
  styleUrls: ['./gallery-layout.component.css'],
  animations: [
		trigger('slideInOut', [
			state('in', style({
        
				transform: 'translate3d(0,0,0)'
			})),
			state('out', style ({
				transform: 'translate3d(100%,0,0)'
			})),
			transition('in => out', animate('400ms ease-in-out')),
			transition('out => in', animate('400ms ease-in-out'))
    ]),
    trigger('expandNav', [
			state('in', style({
        "width": 'calc(45vw)',
        left: '55vw',
        'background-color': '#ffffff00'
			})),
			state('out', style ({
        width: '100%',
        left: '0',
        'background-color': 'white'
        
			})),
			transition('in => out', animate('400ms ease-in-out')),
			transition('out => in', animate('400ms ease-in-out'))
		]),
		trigger('scaleBack', [
			state('in', style({
        "width": 'calc(55% - 55px)',
         height: '100%',
         top: 0
         
			})),
			state('out', style ({
        width: 'calc(100% - 55px)',
        height: 'calc(100% - 55px)',
        top: '30px'
			})),
			transition('in => out', animate('400ms ease-in-out')),
			transition('out => in', animate('400ms ease-in-out'))
		]),
		//zoomInAnimation

	]
})
export class GalleryLayoutComponent implements OnInit {
  layoutSettings = {
    width: 0,
    layout: true,
    iconSize: 1
  };

  menuState = "in";
  private layoutOptions = ['Grid','Split'];
  private iconOptions = ['Small', 'Medium', 'Large'];
  viewForm: FormGroup
  mapOn = true;
  constructor(private formBuilder: FormBuilder) { }

  ngOnInit() {
    this.viewForm = this.formBuilder.group({
      'size': ['Medium'],
      'layout': ['Split']
    });
    this.viewForm.valueChanges.subscribe(data => this.onValueChange(data));
  }

  onValueChange(data) {
    console.log(data);
    this.layoutSettings.iconSize = this.iconOptions.indexOf(data.size);
    //this.layoutSettings.layout = this.layoutOptions.indexOf(data.layout);
    /*
    if (this.layoutSettings.layout == 0) {
      this.layoutSettings.width = 100;
    }
    else {
      this.layoutSettings.width = 55;
    }*/
  }

  toggleMap() {
		console.log("Should close");
    //
    
    this.mapOn = !this.mapOn;
    if (this.mapOn) {
      this.layoutSettings.layout = true;
    }
    this.menuState = this.menuState === 'out' ? 'in' : 'out';
    
  }

  expandGrid(e) {
    if (!this.mapOn) {
      this.layoutSettings.layout = false;
    }
    
    this.layoutSettings.iconSize = (this.layoutSettings.iconSize + 1) % 2;
    console.log(this.layoutSettings.layout)
    console.log("Changed");
  }

}



