import { Component, OnInit, Renderer2, ViewChild, ElementRef,Input, SimpleChanges,ChangeDetectionStrategy, NgZone,  ChangeDetectorRef} from '@angular/core';
import {GridService} from './grid.service';

import { Observable, ReplaySubject } from 'rxjs';
import { Subject }    from 'rxjs/Subject';
import {GalleryService} from '../../../services/gallery.service';
@Component({
  selector: 'app-grid',
  templateUrl: './grid.component.html',
  styleUrls: ['./grid.component.css'],
})
export class GridComponent implements OnInit {
  @ViewChild('mediacontainer') private myMediaContainer: ElementRef;
  @Input() layoutStyle = 1;
  @Input() iconSize = 0;

  clientWidth = 1000;
  mediaData = [];
  onScrolling = new Subject<any>();
  clientHeight = 0;
  lastPos: number = 2000;
  clientMonths = [];
  currentDisplay = {start: 0, end: 3, indices: []};
  scrollLine = 0;
  scrollHandler: any;
  totalHeight = 0;
  testData = [];
  activeIndexes = [];
  activeRowsIndices = [];
  allRows = [];
  //currentSection = 0;
  //currentScroll = 0;
  fakeArray: any[];
  sections = [];
  subs= [];
  scrollDetection: any;
  loaded = false;
  constructor(private service: GridService, private render:Renderer2, private galleryService: GalleryService, private _zone: NgZone, private cdRef:ChangeDetectorRef) {
    this.subs.push(this.onScrolling.debounceTime(25).distinctUntilChanged().subscribe((evt) => this._onScroll2()));
    
   }

  ngOnInit() {
   
    this.subs.push( this.galleryService.onMapChanged.subscribe(resp => this.onMapChange(resp)));
    //this.myMediaContainer.nativeElement.clientWidth;
    
  }
  trackRowFn(index, element) {
    return element;
  }

  ngAfterViewInit() {
    //Called after ngAfterContentInit when the component's view has been initialized. Applies to components only.
    //Add 'implements AfterViewInit' to the class.
     this._zone.runOutsideAngular(() => {this.scrollDetection = this.render.listen(this.myMediaContainer.nativeElement, 'scroll', (evt) => this.onScrolling.next(evt)) });
  }
  
  ngOnChanges(changes: SimpleChanges) {
    //Called before any other lifecycle hook. Use it to inject dependencies, but avoid any serious work here.
    //Add 'implements OnChanges' to the class.
    console.log(changes);
    console.log(this.iconSize)
    
    if (changes.iconSize && this.loaded) {
      console.log(changes.iconSize)
      this.renderSections();
    }
    if (changes.layoutStyle && this.loaded) {
      console.log(changes.layoutStyle)
      this.resizeGrid();
    }
  
    
  }

  dayByFn(index, section) {
    return section.photos.length
  }



  onMapChange(bounds) {
    //console.log(bounds);
    this.loaded = false;
    let hasChanged = false;
    if (bounds == null) {
      for (let i in this.mediaData) {
        this.mediaData[i]['show'] = true;
        hasChanged = true;
      }
    }
    else {
      let temp = [];
      for (let i in this.mediaData) {
        let media = this.mediaData[i];
        ////console.log("" + (bounds.min.x >= media.gpslat) + " " + (bounds.max.x <= media.gpslat) + " " + (bounds.min.y >= media.gpslong) +  " "+ (bounds.max.y <= media.gpslong))
        ////console.log("" + media.gpslat + " " + media.gpslong);
        let before = media.show;
        if ((bounds.min.x >= media.gpslat) && 
            (bounds.max.x <= media.gpslat) &&
            (bounds.min.y <= media.gpslong) &&
            (bounds.max.y >= media.gpslong)) {
              media.show = true;
            }
        else {
          media.show = false;
        }
        if (media.show != before) {
          hasChanged = true;
        }
      }
    }
    if (hasChanged) {
      this._zone.run(() => {this.reRenderSections()});
      this.cdRef.detectChanges();
    }
    else {
      this.loaded = true;
    }
    
    //this.sections.render(this.iconSize);
    //this.changed = !this.changed;
  }

  ngAfterContentInit() {
    //Called after ngOnInit when the component's or directive's content has been initialized.
    //Add 'implements AfterContentInit' to the class.
    console.log(this.myMediaContainer);
    this.galleryService.fetchPreData();
    this.subs.push(this.galleryService.data$.subscribe(resp =>  this.prepareData(resp)));
  }

  scrollJump(loc) {
    this.myMediaContainer.nativeElement.scrollTop = loc;
    this._onScroll2();
  }
  
  ngOnDestroy() {
    //Called once, before the instance is destroyed.
    //Add 'implements OnDestroy' to the class.
    console.log("Grid destroyed");
    this.scrollDetection();
    for (let i = 0; i < this.subs.length; i++) {
      this.subs[i].unsubscribe();
    }
  }


  reRenderSections() {
    //console.log(this.myMediaContainer.nativeElement);
    this.loaded = false;
    this.clientHeight = this.myMediaContainer.nativeElement.clientHeight;
    this.clientWidth = this.myMediaContainer.nativeElement.clientWidth;
    this.fakeArray = this.service.onMapMove();
    this.clientMonths = this.service.getMonths();
    this.totalHeight = this.service.getTotalHeight();
    this._onScroll2();
    this.loaded = true;
    this.cdRef.detectChanges();
    console.log(this.totalHeight);
  }

  renderSections() {
    this.fakeArray = []
    this.cdRef.detectChanges();
    this.loaded = false;
    //console.log(this.myMediaContainer.nativeElement);
    this.clientHeight = this.myMediaContainer.nativeElement.clientHeight;
    this.clientWidth = this.myMediaContainer.nativeElement.clientWidth;
    this.fakeArray = this.service.requestRender(this.clientWidth, this.iconSize, this.clientHeight);
    this.totalHeight = this.service.getTotalHeight();
    this.clientMonths = this.service.getMonths();
    this._onScroll2();
    this.loaded = true;
    //this.sections.display = true;
    
  }

  ngAfterViewChecked() {
    //Called after every check of the component's view. Applies to components only.
    //Add 'implements AfterViewChecked' to the class.
    //console.log("checked grid");
    
  }


  resizeGrid() {
    this.fakeArray = [];
    this.cdRef.detectChanges();
    this.loaded = false;
    //this.sections = this.service.buildDays(this.myMediaContainer.nativeElement.clientWidth);
    console.log(this.sections);
    this.renderSections();
    ////console.log(this.service.getMonths());
    this._onScroll2();
    this.loaded = true;
    
  }




  prepareData(data) {
    this.fakeArray = [];
    this.cdRef.detectChanges();
    this.loaded = false;
    this.service.resetGrid();

    this.mediaData = data.slice(0,data.length);
    let groups = this.service.buildSections(this.mediaData);
    this.sections = this.service.buildDays(this.myMediaContainer.nativeElement.clientWidth);
    console.log(this.sections);
    this.renderSections();
    //this.sections.render(this.iconSize);
    this.loaded = true;
    
    this._onScroll2();
    
  }
  /*
  onScroll(e) {
      if (!this.scrollTimer) {
          this.scrollTimer = setTimeout((e) => this._onScroll(e), 100);
      }
  }
  */

  updateView() {
    this.cdRef.detectChanges();
  }

  groupTrackBy(index, group) {
    return group.height;
  }

  _onScroll2() {
    var scrollTop = this.myMediaContainer.nativeElement.scrollTop;
    this.service.scrollCheck(scrollTop);
    this._zone.run(() => {this.scrollLine = this.myMediaContainer.nativeElement.scrollTop * (this.clientHeight / this.totalHeight)});
    
  }

    
    
      

}



