import { Injectable } from '@angular/core';
import { Subject }    from 'rxjs/Subject';
import {GridSection, GridDay, GridPlacement, GridScroll} from './grid-section';
import { nextTick } from 'q';

@Injectable()
export class GridService {
  private months: any[] = [];
  private items: any[] = [];
  private sortedItems: any[] = [];
  private rowUpdate = new Subject<any[]>();
  public onRowUpdate = this.rowUpdate.asObservable();
  groupedItems: any;
  groupV = [];
  private loadedGrid = false;
  gridPlacement: GridPlacement;
  totalHeight: number;
  updateTimer: number;
  clientMonths: any[];
  allRows: any[];
  constructor() { }
  updates = [];

  stillUpdating: boolean = false;

  getMonths() {
    return this.gridPlacement.getMonths();
  }

  requestRender(viewWidth, iconSize, viewHeight) {
    let sizeOptions = [150,200,300];
    let size = sizeOptions[iconSize];
    if (!this.loadedGrid) {
      this.gridPlacement = new GridPlacement(viewWidth, size, this.groupedItems, viewHeight);
      this.loadedGrid = true;
    }
    let fakeArray = this.gridPlacement.init(viewWidth, size);
    return fakeArray;
    
  }

  resizeGrid(viewWidth, iconSize, viewHeight) {
    let sizeOptions = [150,200,300];
    let size = sizeOptions[iconSize];
    let fakeArray = this.gridPlacement.resizedGrid(viewWidth, size);
    
    return fakeArray;
    
  }

  onMapMove() {
    let fakeArray = this.gridPlacement.checkDisplay();
    this.rowUpdate.next(null);
    return fakeArray;
  }

  getTotalHeight() {
    return this.gridPlacement.getTotalHeight();
  }

  getRow(index) {
    return this.gridPlacement.getRow(index);
  }

  updateRows() {
    this.stillUpdating = true;
    this.updateLoop()
    
  }

  updateLoop() {
    setTimeout(() => {
      
      if (this.updates.length > 0) {
        let rowIndex = this.updates.shift();
        this.rowUpdate.next(rowIndex);
        this.updateLoop();
      }
      else {
        this.stillUpdating = false;
      }
    }, 25);
    
  }
  


  scrollCheck(scrollTop) {
    let nextUpdates = this.gridPlacement.scrolled(scrollTop);
    if (nextUpdates) {
      for (let i in nextUpdates) {
        this.updates.push(nextUpdates[i]);
      }
      if (!this.stillUpdating) {
        this.updateRows();
      }
    }
    
  }

  renderGroups(viewWidth) {
    for (let i = 0; i < this.groupedItems.length; i++) {
      let gridGroup = this.groupedItems[i];
      gridGroup.render(0);
    }
  }

  resetGrid() {
    this.loadedGrid = false;
  }





  buildDays(viewWidth) {
    let size = 1;
    var index = 0;
      var days = this.items; //PLACEHOLDER DATA SHOULD BE COMING FROM INPUT
      //var view_width = 1711; //PLACEHOLDER DATA SHOULD BE COMING FROM INPUT
      var prev_date = new Date(days[0].date);
      let gridDays = [];
  
      var count = 0;
      const items_per_group = 30;
      var gridGroups = [];
      var day_group = new GridSection(viewWidth, {top: 50, left:0});
      //day_group.position.top = 30; //BAD BAD BAD hacking to get the top date to show.
      ////console.log(days);
      var prev_month_height = 0;
      var currentMonth  = {date: prev_date, height: 0};

      let cSize = 225;
          switch (size) {
            case 0:
              cSize = 150;
              break;
            case 1:
              cSize = 225;
              break;
            case 2:
              cSize = 300;
              break;
          }
      //Iterate through each day
      while (index < days.length) {
        //let gridGroup = new GridGroup(viewWidth);
        count = 0;
        while (count < items_per_group && index < days.length) {
          count += days[index]['content'].length; 
          let current_date = new Date(days[index].date);

          

          let items = days[index]['content'];
          ////console.log(items);
          let currentDay = new GridDay((viewWidth - 10 ), days[index].date, {top: 5, left:5}, {top: 20, left:10});
          let item_index = 0;
          //Iterate within the day
          while (item_index < items.length) {
            ////console.log(items[item_index]);
            items[item_index]['show']=true;
            currentDay.addPhoto(items[item_index]);
            //item_group.addPeople(items[item_index].people);
            //item_group.addAddress(items[item_index].address.building);
            item_index++;   
          }
          count += days[index]['content'].length; 
          index++;
          ////console.log(item_group);
          gridGroups.push(currentDay);
          // //console.log(day_group.height + group.height + item_group.height);
        }
        ////console.log(day_group.top);
        //gridGroups.push(gridGroup);

      }  
      this.groupedItems = gridGroups;
      //Dont forget left over days
      //console.log("Done calc");
      return gridGroups;
  }

  buildSections(data) {
    var items = data
    //items = items.concat(data);
    
    items.forEach( function(a) {
      a.date = new Date(a.date);
    });

    items.sort(function(a,b) {
      return b.date.valueOf() - a.date.valueOf();
    });
    
    //console.time('sort');
    
    this.sortedItems = []
    this.sortedItems = this.sortedItems.concat(items);
    var index = 0;
    ////console.log(items);
    var prev_date = new Date(items[0].date);
    var time_zone = prev_date.getTimezoneOffset() * 1000 * 60;
    var days = []
    var count = 0;
    var groups = [];
    var end_time;
    var sections = [];

    while (index < items.length) {
      let end_index = index;
      //let section = new GridSection();
      let day_data = {};
      day_data['content'] = [];
      let temp_date = items[index].date - time_zone;
      end_time = new Date(items[index].date.getFullYear(), items[index].date.getMonth(), items[index].date.getDate() - 1);
      end_time = end_time.valueOf();
      //Find the last item of the day
      //console.log(index + '|' + items[index].date + ':' + end_time);
      
      while (end_index < items.length && items[end_index].date.valueOf() > end_time.valueOf()) {
        ////console.log(items[index])
        ////console.log(items[index].date + ' ' + index)
        end_index++;        
        //index++;       
      }
      //console.log(index + '|' + end_time);
      day_data['date'] = items[index].date;
      //section.name = items[index].date; 
      //section.photos = items.slice(index, end_index);
      day_data['content'] = items.slice(index, end_index);        //Seperate the items
      index = end_index;
      days.push(day_data);
      //sections.push(section);
    }
    ////console.log(days);
    this.items = days;
    //console.timeEnd('sort');
    return days;
  }


  renderSections(viewWidth) {
    let size = 1;
    var index = 0;
      var days = this.items; //PLACEHOLDER DATA SHOULD BE COMING FROM INPUT
      //var view_width = 1711; //PLACEHOLDER DATA SHOULD BE COMING FROM INPUT
      var prev_date = new Date(days[0].date);
      
  
      var count = 0;
      const items_per_group = 30;
      var day_group = new GridSection(viewWidth, {top: 50, left:0});
      //day_group.position.top = 30; //BAD BAD BAD hacking to get the top date to show.
      ////console.log(days);
      var prev_month_height = 0;
      var currentMonth  = {date: prev_date, height: 0};

      let cSize = 225;
          switch (size) {
            case 0:
              cSize = 150;
              break;
            case 1:
              cSize = 225;
              break;
            case 2:
              cSize = 300;
              break;
          }
      //Iterate through each day
      while (index < days.length) {
        let group = new GridSection(viewWidth, {top: 25, left:5});
        group.display = false;

        count = 0;
        while (count < items_per_group && index < days.length) {
          count += days[index]['content'].length; 
          let current_date = new Date(days[index].date);

          //Index the months for later
          if (current_date.getMonth() != prev_date.getMonth()) {
            currentMonth.height = day_group.size.height + group.size.height - prev_month_height;
            prev_month_height = day_group.size.height + group.size.height;
            this.months.push(currentMonth);
            currentMonth = {date: current_date, height: 0};
            prev_date = current_date;
          }
          

          let items = days[index]['content'];
          ////console.log(items);
          let item_group = new GridSection(viewWidth , {top: 0, left:0});
          item_group.name = days[index].date;
          let item_index = 0;
          //Iterate within the day
          while (item_index < items.length) {
            ////console.log(items[item_index]);
            items[item_index]['show']=true;
            item_group.addPhoto({top: 0, left:0, width: items[item_index].aspect_ratio * cSize, height:cSize, content: items[item_index], index: item_index+count, isItem: true});
            //item_group.addPeople(items[item_index].people);
            //item_group.addAddress(items[item_index].address.building);
            item_index++;   
          }
          count += days[index]['content'].length; 
          index++;
          ////console.log(item_group);
          group.addChild(item_group);
          // //console.log(day_group.height + group.height + item_group.height);
        }

        day_group.addChild(group);
        ////console.log(day_group.top);
        //groups.add(day_group);
        
      }  
      this.groupedItems = day_group;
      //Dont forget left over days
      //console.log("Done calc");
      return day_group;
  }

}



