import { Component, OnInit, Input, ChangeDetectorRef } from '@angular/core';
import { ActivatedRoute, Router }   from '@angular/router';
import {SelectionService} from '../selection.service';
import {PhotoApiService} from '../../services/photo-api.service';
import {GalleryService} from '../../services/gallery.service';
import {PhotoUrlService} from '../../services/photo-url.service';

@Component({
  selector: 'app-gallery-card',
  templateUrl: './gallery-card.component.html',
  styleUrls: ['./gallery-card.component.css'],
 
})
export class GalleryCardComponent implements OnInit {

  @Input() photo: any;
  @Input() frame: any;
  show_check = false;
  isChecked = false;
  previewTimer = null;
  isMouseIn = false;
  showPreview: boolean = false;
  showImage: boolean = true;
  rated = false;
  rateType = ""

  constructor(private router: Router, private photoUrlService: PhotoUrlService, private selectionService: SelectionService, private cdRef: ChangeDetectorRef, private galleryService: GalleryService, private photoApiService:PhotoApiService, private route: ActivatedRoute) {
    
  }

  selectPhoto() {
    this.selectionService.selectMedia(this.photo);
  }

  ngAfterViewChecked() {
    //Called after every check of the component's view. Applies to components only.
    //Add 'implements AfterViewChecked' to the class.
    //console.log("checked card");
  }


  onMouseEnter() {
    this.showPreview = !this.showPreview;   
    this.galleryService.photoHover(this.photo);
  }

  onMouseLeave() {
    this.showPreview = !this.showPreview;
    this.galleryService.photoHover(null);    
  }



  ngOnInit() {
  }

  togglePlay() {
      this.showPreview = !this.showPreview;    
  }

  previewPhoto() {
    if (this.selectionService.photosSelected) {
      this.selectPhoto();
      
    }
    else {
      this.galleryService.photoClicked(this.photo);
    
    }
  }

  openPhoto() {
    this.router.navigate(['media',this.photo.id], {relativeTo: this.route});
  }


  onRate(rating){
    this.photo.rating=rating;
    this.photoApiService.updatePhoto(this.photo).subscribe(resp => this.onUpdate(resp));
  }

  onUpdate(resp) {
    console.log(resp);
    let types = ['archive', 'delete', 'favorite_border']
    this.rateType = types[resp.rating % 3];
    this.rated = true;

  }

  

  doneExpand(e) {
    
  }

  showCheck(){
    return this.showPreview || this.photo.selected;
  }
  

}



