import { Component, OnInit, ViewChild, ElementRef, ChangeDetectorRef } from '@angular/core';
import {trigger, state, style, transition, animate} from '@angular/animations';
import { ActivatedRoute, Params, Router,  }   from '@angular/router';

import {PhotoApiService} from '../../services/photo-api.service';
import {PhotoUrlService} from '../../services/photo-url.service';
import {GalleryService} from '../../services/gallery.service';
@Component({
  selector: 'app-media-modal',
  templateUrl: './media-modal.component.html',
  styleUrls: ['./media-modal.component.css'],
  animations: [
		trigger('slideInOut', [
			state('in', style({
				transform: 'translate3d(0,0,0)'
			})),
			state('out', style ({
				transform: 'translate3d(100%,0,0)'
			})),
			transition('in => out', animate('400ms ease-in-out')),
			transition('out => in', animate('400ms ease-in-out'))
		]),
		trigger('scaleBack', [
			state('in', style({
				"width": 'calc(100% - 360px)', height: '100%'
			})),
			state('out', style ({
				width: '100%', height: '100%'
			})),
			transition('in => out', animate('400ms ease-in-out')),
			transition('out => in', animate('400ms ease-in-out'))
		]),
		//zoomInAnimation

	]
})
export class MediaModalComponent implements OnInit {
	@ViewChild('image') private myImageContainer: ElementRef;
  display: boolean;
	display_map = false;
	mediaItem: any;
	menuState:string = 'in';
	maxWidth: any;
	_winRef: Window;
	maxHeight: any;
	bload = false;
	stories: any[] = [];
	displayStories: boolean = false;
	displayOptions: boolean = false;
	objectDetections: any[];
	objectBoxes: any[];

  constructor(
	  private route: ActivatedRoute,
		private router: Router,
    private photoApiService: PhotoApiService,
		private galleryService: GalleryService,
		private photoUrlService: PhotoUrlService,
		private cdr: ChangeDetectorRef
	) {
		
	}

  ngOnInit() {
    this.route.params.subscribe(params => this.loadMedia(params['id']));
  }


	loadMedia(id) {
		console.log(this.route.data);
		this.photoApiService.getMediaByID(id).subscribe(resp => this.mediaItem = resp);
		this.photoApiService.getObjectDetections(id).subscribe((resp) => this.loadObjectDetections(resp));  
		this.objectBoxes = [];
		//this.service.setCurrentIndex(id);
		
		
	}

	showDetections() {
		if (this.mediaItem.kind == "image" && this.myImageContainer !== undefined) {
			console.log(this.myImageContainer);
			var height = this.myImageContainer.nativeElement.clientHeight;
			var width = this.myImageContainer.nativeElement.clientWidth;
			var leftOffset = this.myImageContainer.nativeElement.offsetLeft;
			var topOffset = this.myImageContainer.nativeElement.offsetTop;
			
			for (let i =0; i < this.objectDetections.length; i++) {
				let obj = this.objectDetections[i];
				let boxHeight = (height/100) * (obj.box[2] - obj.box[0]);
				let boxWidth =  (width/100) * (obj.box[3] - obj.box[1]);
				let box = {top: (obj.box[0]*height/100) + topOffset , left: (obj.box[1]*width/100) + leftOffset, height: boxHeight, width: boxWidth, name: obj.class_name}
				this.objectBoxes.push(box);
			}
			console.log(this.objectBoxes);
			this.cdr.detectChanges();
		}
	}
	



	nextItem(dir) {
		let item = this.galleryService.nextItem(this.mediaItem, dir);
		this.router.navigate(['photos','media',item]);
	}


  gotoDetails() {
		console.log("Should close");
		this.menuState = this.menuState === 'out' ? 'in' : 'out';
	}
	
	loadObjectDetections(resp) {
		console.log(resp);
		resp.forEach(element => {
				if (!element.person) {
						element.person = {'name': ""}
				}
		});
		this.objectDetections = resp;
}


  close() {
		this.router.navigate(['../../'], {relativeTo: this.route});
  }

}



