import { Injectable } from '@angular/core';
import { Headers, Http, Response } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/Rx';
import 'rxjs/add/observable/throw';

@Injectable()
export class ApiService {
  headers: Headers = new Headers({
    'Content-Type': 'application/json',
    'Authorization': 'JWT ',
    Accept: 'application/json'
  });
  // https://media-database.herokuapp.com  http://localhost:3030
  api_url: string = 'https://young-everglades-73675.herokuapp.com';

  public loggedin: boolean = false;
  public token: string = "";
  public username: string = ""
  

  constructor(private http: Http) {
  }

  public setBaseUrl(url) {
    this.api_url = url;
  }

  private getJson(response: Response) {
    console.log(response.json());
    
    return response.json();
  }


  private updateAuth() {
    this.headers.set('Authorization', 'JWT ');
  }

  public setAuth(token) {
    this.headers.set('Authorization', 'JWT ' + token);
  }

  getMap(path: string): Observable<any> {
    return this.http.get(`${path}`, { headers: this.headers })
      .map(this.checkForError)
      .catch(err => Observable.throw(err))
      .map(this.getJson)
  }

  private checkForError(response: Response): Response {
    if (response.status >= 200 && response.status < 300) {
      return response;
    } else {
      var error = new Error(response.statusText)
      error['response'] = response;
      console.error(error);
      throw error;
    }
  }

  put(path: string, data): Observable<any> {
    console.log(this.api_url + path);
    return this.http.put(
      `${this.api_url}${path}`,
      JSON.stringify(data),
      { headers: this.headers }
    )
    .map(this.checkForError)
    .catch(err => Observable.throw(err))
    .map(this.getJson)
  } 

  get(path: string, extras = {}): Observable<any> {
    console.log(this.api_url + path);
    return this.http.get(`${this.api_url}${path}`, { headers: this.headers, params: extras  })
    .map(this.checkForError)
    .catch(err => Observable.throw(err))
    .map(this.getJson)
  }

  login(username: string, password: string) {
    let headers = new Headers({
    'Accept': 'application/json',
    'Content-Type': 'application/json',
    });
    return this.http.post(`${this.api_url}/api-token-auth/`, JSON.stringify({ username: username, password: password }), {headers})
            .map((response: Response) => {
                // login successful if there's a jwt token in the response
                let token = response.json() && response.json().token;
                if (token) {
                    // set token property
                    this.token = token;
                    this.username = username;

                    // store username and jwt token in local storage to keep user logged in between page refreshes
                    localStorage.setItem('currentUser', JSON.stringify({ username: username, token: token }));
                    this.setAuth(token)
                    this.loggedin = true;
                    //this.api.setAuth(token);
                    //this.apiService.setAuth(this.token);
                    // return true to indicate successful login
                    return true;
                } else {
                    // return false to indicate failed login
                    return false;
                }
            });
  }



  post(path: string, body): Observable<any> {
    return this.http.post(
      `${this.api_url}${path}`,
      JSON.stringify(body),
      { headers: this.headers }
    )
    .map(this.checkForError)
    .catch(err => Observable.throw(err))
    .map(this.getJson)
  }

  delete(path: string): Observable<any> {
    return this.http.delete(
      `${this.api_url}${path}`,
      { headers: this.headers }
    )
    .map(this.checkForError)
    .catch(err => Observable.throw(err))
    .map(this.getJson)
  }
}



