import { Component, OnInit, Input, Output, EventEmitter, ViewChild, ElementRef, HostListener} from '@angular/core';
import { ActivatedRoute, Params }   from '@angular/router';
import { Location }                 from '@angular/common';
import { Router } from '@angular/router';

import {GalleryService} from '../../../services/gallery.service';
@Component({
  selector: 'app-media-modal-details',
  templateUrl: './media-modal-details.component.html',
  styleUrls: ['./media-modal-details.component.css']
})
export class MediaModalDetailsComponent implements OnInit {
  @Input() media_item;
  detections = [];

  constructor(private galleryService: GalleryService, private router: Router) { }

  ngOnInit() {
    
  }

  addTag(item) {
    let query = this.galleryService.getQuery();
    query.tags.push(item);
    this.galleryService.updateQuery(query);
    this.router.navigate(['photos']);
  }


  tagPerson(detection) {
    console.log(detection);
    detection.correct = true;
    this.galleryService.tagPerson(detection).subscribe((resp) => detection = resp);
  }


  ngOnChanges(changes) {
    //Called before any other lifecycle hook. Use it to inject dependencies, but avoid any serious work here.
    //Add 'implements OnChanges' to the class.
    if (this.media_item) {
            this.galleryService.getDetections(this.media_item.id).subscribe((resp) => this.loadDetections(resp));  
        }
  }

  loadDetections(resp) {
        console.log(resp);
        resp.forEach(element => {
            if (!element.person) {
                element.person = {'name': ""}
            }
        });
        this.detections = resp;
    }

}



