import { Injectable } from '@angular/core';

@Injectable()
export class SelectionService {
  selectedMedia: any[];
  photosSelected: boolean = false;

  constructor() {
    this.selectedMedia = [];
   }

  selectMedia(media) {
    if (media.selected == null || !media.selected) {
      media.selected = true;
      this.photosSelected = true;
      if (this.selectedMedia.indexOf(media) == -1) {
        this.selectedMedia.push(media);
      }
    }
    else {
      media.selected = false;
      let index = this.selectedMedia.indexOf(media);
      this.selectedMedia.splice(index, 1);
    }
    console.log(media);
  }


  endEdit() {
    for (let i = 0; i < this.selectedMedia.length; i++) {
      let media = this.selectedMedia[i]
      media.selected = false;
    }
    this.selectedMedia = [];
    this.photosSelected = false;
  }

  
}



