import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-manager-export',
  templateUrl: './manager-export.component.html',
  styleUrls: ['./manager-export.component.css']
})
export class ManagerExportComponent implements OnInit {
  options = [{'name': 'All', 'value': ""}, {'name': 'Photos', 'value': "image"}, {'name': 'Videos', 'value': "video"} ];
  ratingTypes = [{'name': 'Current', 'value': 0}, {'name': 'Archived', 'value': 3}, {'name': 'Showcase', 'value': 5}, {'name': 'Deleted', 'value': 1},  ];
  exportParams = {type: 'image', rating: 5}
  constructor() { }

  ngOnInit() {
  }

  updateType(type) {
    this.exportParams.type = type;
  }

  updateRating(rating) {
    this.exportParams.rating = rating;

  }

}



