import { Component, Output, EventEmitter, OnInit, Input, NgZone } from '@angular/core';


declare var L: any;

@Component({
  selector: 'app-shared-map',
  templateUrl: './map.component.html',
  styleUrls: ['./map.component.css']
})
export class SharedMapComponent implements OnInit {
  @Input() cluster: any = [];

  @Output() mapReady = new EventEmitter<any>();
  private mymap: any;
  private markers: any;
  private layer: any;
  
  constructor(private _zone: NgZone) { }

  ngOnInit() {
    
   

    this.initMap();
  }

  ngOnChanges() {
    
    if (this.mymap != null) {
      this.markers.clearLayers();
      this.markers.addLayers(this.cluster);
      //this.loaded = true;
      this.mymap.addLayer(this.markers);
      console.log(this.cluster);
      console.log(this.markers);
    }
    

    
  }

  initMap() {
    console.log(L);
    (L as any).mapbox.accessToken = 'pk.eyJ1Ijoia3lyb25lIiwiYSI6ImNpeGtvZmYxYzAwNGszMmwxenhibnJlYzcifQ.p8RUBkfy_5vLbNOtO71E1w';
    this.mymap = this._zone.runOutsideAngular(() => (L  as any).mapbox.map('mapid','https://api.mapbox.com/v4/mapbox.mapbox-streets-v7.json', {zoomControl: false, zoomAnimation: false, renderer: L.canvas()}).setView([36.2168, -81.6746], 13,));
    var styleLayer = (L as any).mapbox.styleLayer("mapbox://styles/kyrone/cixrz4eda001l2rn2l2idkf9o")
        .addTo(this.mymap);
    
    this.markers = (L as any).markerClusterGroup({ spiderfyDistanceMultiplier: 3 });
    //this.layer = L.tileLayer('https://api.mapbox.com/styles/v1/kyrone/cixrz4eda001l2rn2l2idkf9o/tiles/256/{z}/{x}/{y}?access_token=pk.eyJ1Ijoia3lyb25lIiwiYSI6ImNpeGtvZmYxYzAwNGszMmwxenhibnJlYzcifQ.p8RUBkfy_5vLbNOtO71E1w', {zoomControl: false})
    this.mapReady.next(this.mymap);
    this.markers.addLayers(this.cluster)
    this.mymap.addLayer(this.markers);
    //this.mymap.addLayer(this.layer)
    
  }

}



