import { Component, OnInit, Input, Output, EventEmitter, SimpleChanges, ChangeDetectorRef } from '@angular/core';
import {trigger, state, style, transition, animate} from '@angular/animations';

import {GridService} from '../grid.service';

@Component({
  selector: 'app-grid-scroll-bar',
  templateUrl: './grid-scroll-bar.component.html',
  styleUrls: ['./grid-scroll-bar.component.css'],
  animations: [
		trigger('fade', [
			state('in', style({
			})),
			state('out', style ({
				opacity: '0'
			})),
			transition('in => out', animate('1000ms ease-in-out')),
			transition('out => in', animate('50ms ease-in-out'))
		]),

	]
})
export class GridScrollBarComponent implements OnInit {
  @Input() months;
  @Input() height;
  @Input() width;
  @Input() groupHeight;
  @Input() scrollLocation;
  scrollSize = 1;
  fadeTimer = null;
  fadeState = "in";
  hover = false;
  @Output() onJump = new EventEmitter<any>();

  constructor(private gridService: GridService, private cdRef: ChangeDetectorRef) { }

  ngOnInit() {
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes['groupHeight']) {
      this.scrollSize = (this.height) * (this.height / this.groupHeight);
      if (this.scrollSize > this.height) {
        this.scrollSize = this.height;
      }
    }
    this.softFade();
    
  }

  hoverOn() {
    this.fadeState = 'in';
    this.hover = true;
  }

  hoverOff() {
    this.hover = false;
    this.softFade();
  }

  fadeIn() {
    this.fadeState = 'in';
  }

  softFade() {
    this.fadeState = 'in';
    //this.cdRef.detectChanges();
    if (!this.fadeTimer) {
      this.fadeTimer = setTimeout((e) => this.fadeOut(e), 5000);
    }
  }

  fadeOut(e) {
    if(!this.hover) {
      this.fadeState = 'out';
      
    }
    this.fadeTimer = null;
    
  }



  getMonthHeight(month) {
    return (month.end - month.start) * (this.height / this.groupHeight);
  }

  onClick(e) {
    let data = (e.clientY - 50) * (this.groupHeight / (this.height ) );
    this.onJump.emit(data);
  }

}



