import { Component, OnInit } from '@angular/core';
import {AuthService} from '../../services/auth.service';
import {PhotoApiService} from '../../services/photo-api.service';
@Component({
  selector: 'app-manager-home',
  templateUrl: './manager-home.component.html',
  styleUrls: ['./manager-home.component.css']
})
export class ManagerHomeComponent implements OnInit {
  files = [];
  method: string = 'POST';
  url: string= "https://young-everglades-73675.herokuapp.com/upload";
  public progress: number = 0;
  name: string = "files";
  

  constructor(private authService: AuthService, private photoApiService:PhotoApiService) { }


  ngOnInit() {
  }

  runCommand(cmd) {
    this.photoApiService.execCommand(cmd).subscribe(resp => console.log(resp));
  }

  

  isFileSelected(file: File): boolean{
      for(let sFile of this.files){
          if((sFile.name + sFile.type + sFile.size) === (file.name + file.type+file.size)) {
              return true;
          }
      }   
    
      return false;
  }

  onFileSelect(event) {
    let files = event.dataTransfer ? event.dataTransfer.files : event.target.files;
    for(let i = 0; i < files.length; i++) {
        let file = files[i];
        
        if(!this.isFileSelected(file)){
          this.files.push(files[i]);
        }
    }
  }

  upload() {
    let xhr = new XMLHttpRequest(),
    formData = new FormData();

    for(let i = 0; i < this.files.length; i++) {
      formData.append(this.name, this.files[i], this.files[i].name);
    }
    xhr.open(this.method, this.url, true);
    xhr.setRequestHeader("Authorization", 'JWT ' + this.authService.token)

    
    xhr.send(formData);
  }

}



