import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import {GalleryService} from '../../services/gallery.service';
import {PhotoApiService} from '../../services/photo-api.service';
@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.css']
})
export class SearchComponent implements OnInit {

  search = {
    tags: [],
    people: [],
    rating: 0,
    duration: [-1,0],
    type: "",
    $limit: 10000,
    private: false,
    "$sort[date]": -1
  };

  searchData = {
    tags: [],
    people: [],
  }

  searchStr = "";

  options = [{'name': 'All', 'value': ""}, {'name': 'Photos', 'value': "image"}, {'name': 'Videos', 'value': "video"} ];

  ratingTypes = [{'name': 'Current', 'value': 0}, {'name': 'Archived', 'value': 3}, {'name': 'Showcase', 'value': 5}, {'name': 'Deleted', 'value': 1},  ];
  durationRanges = [{'name': 'Short', 'value': [0,60]}, {'name': 'Medium', 'value': [61,360]}, {'name': 'Long', 'value': [-1,-1]}];
  bufferInput = [0,0,0,0];
  filteredTags = [];
  filteredPeople = [];

  constructor(private router: Router, private route: ActivatedRoute, private photoApiService: PhotoApiService, private galleryService: GalleryService) { }

  ngOnInit() {
    this.search = this.galleryService.getQuery();
    this.loadKeywords();
    
  }

  goBack() {
        this.router.navigate(['../'], {relativeTo: this.route});
  }

  closeModal(e) {
    console.log(e);
    if (e.target.className == "search-wrapper") {
      this.goBack();
    }
  }

  updateBuffer(data) {
    this.bufferInput[0]= Math.floor(data[0] / 60);
    this.bufferInput[1]= data[0] % 60;

    this.bufferInput[2]= Math.floor(data[1] / 60);
    this.bufferInput[3]= data[1] % 60;
  }

  loadKeywords() {
    this.photoApiService.getTags(this.search).subscribe(resp => {this.searchData.tags = resp; this.filterData()});
    this.photoApiService.getPeople().subscribe(resp => {this.searchData.people = resp; this.filterData()});
  }

  filterData() {
    if (this.searchData.tags) {
      this.filteredTags = this.searchData.tags.slice(0,20);
    }
    if (this.searchData.people) {
      this.filteredPeople = this.searchData.people.slice(0,20);
    }
  }

  onInput(e) {
      let value = e.target.value;
      if (value == "") {
          this.filterData();
      }
      else {
          this.filterKeywords(value);
      }
  }

  filterKeywords(query) {
    console.log(query);
    this.filteredTags = [];
    this.filteredPeople = [];

    let tags = this.searchData.tags;
    console.log(this.searchData.tags);
    for(let i = 0; i < tags.length; i++) {
        let keyword = tags[i].name;
        console.log(keyword);
        if(keyword.toLowerCase().indexOf(query.toLowerCase()) == 0) {
            this.filteredTags.push(tags[i]);
        }
    }
    tags = this.searchData.people;
    for(let i = 0; i < tags.length; i++) {
        let keyword = tags[i].name;
        console.log(keyword);
        if(keyword.toLowerCase().indexOf(query.toLowerCase()) == 0) {
            this.filteredPeople.push(tags[i]);
        }
    }
  }

  updateSearch() {
    this.galleryService.updateQuery(this.search);
    this.loadKeywords();
  }

  addTag(tag) {
    this.search.tags.push(tag);
    this.updateSearch();
  }

  addPerson(person) {
    this.search.people.push(person);
    this.updateSearch();
  }

  updateType(type) {
    this.search.type = type;
    this.updateSearch();
  }

  updateRating(rating) {
    this.search.rating = rating;
    this.updateSearch();

  }

  removeTag(index) {
    this.search.tags.splice(index,1);
    this.updateSearch();
  }

  removePerson(index) {
    this.search.people.splice(index,1);
    this.updateSearch();
  }

}



