import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { GalleryLayoutComponent } from './gallery-layout/gallery-layout.component';
import { GridComponent } from './gallery-layout/grid/grid.component';
import { MapComponent } from './gallery-layout/map/map.component';
import { SearchComponent } from './search/search.component';
import { GalleryCardComponent } from './gallery-card/gallery-card.component';
import { GridSectionComponent } from './gallery-layout/grid/grid-section/grid-section.component';
import {GalleryRoutingModule} from './gallery-routing.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import {NavModule} from '../nav/nav.module';

import {SelectionService} from './selection.service'
import {GridService} from './gallery-layout/grid/grid.service';
import { LayoutOptionsComponent } from './gallery-layout/layout-options/layout-options.component';
import { MediaModalComponent } from './media-modal/media-modal.component';
import { MediaModalDetailsComponent } from './media-modal/media-modal-details/media-modal-details.component';
import { GridScrollBarComponent } from './gallery-layout/grid/grid-scroll-bar/grid-scroll-bar.component';
import { GridDayComponent } from './gallery-layout/grid/grid-day/grid-day.component';
import { GallerySelectionComponent } from './gallery-selection/gallery-selection.component';
import {SharedModule} from '../shared/shared.module';
@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    NavModule,
    SharedModule,
    ReactiveFormsModule,
    GalleryRoutingModule,
  ],
  declarations: [GalleryLayoutComponent, GridComponent, MapComponent, SearchComponent, GalleryCardComponent, GridSectionComponent, LayoutOptionsComponent, MediaModalComponent, MediaModalDetailsComponent, GridScrollBarComponent, GridDayComponent, GallerySelectionComponent],
  providers: [GridService, SelectionService]
})
export class GalleryModule { }



