import { Component, OnInit } from '@angular/core';
import {GalleryService} from '../../services/gallery.service'
import {SelectionService} from '../selection.service';
@Component({
  selector: 'app-gallery-selection',
  templateUrl: './gallery-selection.component.html',
  styleUrls: ['./gallery-selection.component.css']
})
export class GallerySelectionComponent implements OnInit {

  constructor(private galleryService: GalleryService, private selectionService: SelectionService) { }

  ngOnInit() {

  }

  goBack() {
    this.selectionService.endEdit();
  }

  isPhotoSelected() {
    return this.selectionService.photosSelected;
  }

}



