import { NgModule }             from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { GalleryLayoutComponent }    from './gallery-layout/gallery-layout.component';
import {MediaModalComponent} from './media-modal/media-modal.component';
import {SearchComponent} from './search/search.component';
import {AuthGuardService} from '../services/auth-guard.service';
const galleryRoutes: Routes = [
  { path: 'photos',  component: GalleryLayoutComponent, canActivate: [AuthGuardService], canActivateChild: [AuthGuardService],
      children: [
        {
          path: 'media/:id',
          component: MediaModalComponent,
        },
        {
          path: 'search',
          component: SearchComponent,
        }
      ] },
];

@NgModule({
  imports: [
    RouterModule.forChild(galleryRoutes)
  ],
  exports: [
    RouterModule
  ]
})
export class GalleryRoutingModule { }


