import { Component, OnInit, Input,ChangeDetectorRef, ChangeDetectionStrategy } from '@angular/core';
import {GridService} from '../grid.service';
import {GridRow} from '../grid-section';
@Component({
  selector: 'app-grid-section',
  templateUrl: './grid-section.component.html',
  styleUrls: ['./grid-section.component.css'],
  
})
export class GridSectionComponent implements OnInit {
  //@Input() section:GridGroup = null;
  loaded: boolean = false;
  section:GridRow = null;
  changeTimer: any = null;
  @Input() index: number;
  currentIndex: number;
  sub: any;
/*
  @Input('sectionS')
  set sectionS(value) {
    if (!this.section) {
      this.section = value;
      this.loaded= true;
      this.cdRef.detectChanges();
    }

    if (this.section.position.top != value.position.top) {
      this.loaded= false;
      this.section = value;
      this.updateView();
      
      
    }
  }
  */


  constructor(private cdRef: ChangeDetectorRef, private gridService:GridService ) {
    this.sub = this.gridService.onRowUpdate.subscribe((data) => this.checkNew(data));
   }


  checkNew(data) {
    if (data == null) {
      this.updateView();
    }
    else {
      let index = data[this.index];
      if (!this.section || data.old == this.currentIndex) {
        this.updateRow(data.new);
      }
    }
    
    
  }

  updateRow(index) {
    this.currentIndex = index;
    this.section = this.gridService.getRow(index);
    this.updateView();
  }


  updateView() {
    this.loaded = true;
    this.cdRef.detectChanges();
    
    this.changeTimer = null;
  } 

  ngOnInit() {
    this.cdRef.detach();
    this.updateRow(this.index);
  }


  getHeight() {
    return this.section.size.height;
  }

  getWidth() {
    return this.section.size.width;
  }

  getLeft() {
    return this.section.position.left;
  }

  getTop() {
    return this.section.position.top;
  
  }

  ngAfterViewInit() {
    //Called after ngAfterContentInit when the component's view has been initialized. Applies to components only.
    //Add 'implements AfterViewInit' to the class.
    
  }

  photoByFn(index, photo) {
    return photo.photos.length;
  }

  trackGridFn(index, element) {
    return element.position.left;
  }

  ngAfterViewChecked() {
    //Called after every check of the component's view. Applies to components only.
    //Add 'implements AfterViewChecked' to the class.
  }

  ngOnDestroy() {
    //Called once, before the instance is destroyed.
    //Add 'implements OnDestroy' to the class.
    console.log("Destroyed");
    this.sub.unsubscribe();
  }

  

  

}



