import { Injectable } from '@angular/core';

import { Observable, ReplaySubject } from 'rxjs';
import { Subject }    from 'rxjs/Subject';

import {PhotoApiService} from './photo-api.service';

@Injectable()
export class GalleryService {
  path: string = '/media';

  private dataSubject = new ReplaySubject<Response>(1);


  private hoverPhoto = new Subject<any>();
  hoveredPhoto = this.hoverPhoto.asObservable();

  private selectPhoto = new Subject<any>();
  selectedPhoto = this.selectPhoto.asObservable();
  data$: Observable<Response> = this.dataSubject.asObservable();
  dataLocal: any[] = [];
  private mapBounds = new Subject<any[]>();
  public onMapChanged = this.mapBounds.asObservable();
  query = {
        tags: [],
        people: [],
        rating: 5,
        duration: [-1,0],
        type: "",
        $limit: 10000,
        private: false,
        "$sort[date]": -1
    };
        
  constructor(private apiService: PhotoApiService) {}

  fetchPreData() {
    this.apiService.queryMedia(this.query).subscribe(res => this.loadData(res));
  }

  photoHover(photo) {
    this.hoverPhoto.next(photo);
  }

  loadData(res) {
    this.dataSubject.next(res);
    
    this.dataLocal = res.map(function(e) {return e.id});
  }

  photoClicked(photo) {
    this.selectPhoto.next(photo);
  }

  updateQuery(newQuery) {
    this.query = newQuery;
    this.fetchPreData();
  }


  nextItem(media, dir) {
    console.log(media);
    console.log(this.dataLocal);
    let index = this.dataLocal.indexOf(media.id);
    console.log(index);
    return this.dataLocal[(index+dir)];
  }


  getPhotoUrl(photo, preview) {
    
  }
 

  mapChange(data) {
    //console.log(data);
    this.mapBounds.next(data);
  }

  getQuery() {
    return this.query;
  }

  getDetections(mediaID) {
    return this.apiService.getPersonDetections(mediaID);
  }

  tagPerson(detection) {
    return this.apiService.updatePersonDetection(detection);
  }

}



